.weatherBlock {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  width: 100%;
  height: 117px;
  padding: 10px;
  border-radius: 6px;
  background-color: #a2d2ff;
  background-position: 25% 85%;
  background-size: cover;
}
.currPosition {
  font-size: 12px;
  font-weight: 500;
}
.weather {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 25px;
  font-weight: 200;
}
.weatherIcon {
  display: inline-block;
  width: 30%;
}
.description {
  font-size: 11px;
}
@media only screen and (max-width: 768px) {
  .weatherBlock {
    width: 49%;
    min-height: 93px;
    height: 100%;
  }
}
