.main {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex: 1 1 auto;
  width: 100%;
  margin: 130px 0 20px 0;
}
.mainContainer {
  display: flex;
  flex-direction: row;
  column-gap: 20px;
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
}
@media only screen and (max-width: 768px) {
  .main {
    margin: 120px 0 20px 0;
  }
  .mainContainer {
    flex-direction: column;
    row-gap: 10px;
    margin: 0 10px;
  }
}
