.aside {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 20%;
  padding: 5px;
  background-color: #f7ede2;
  border-radius: 6px;
  column-gap: 5px;
  row-gap: 5px;
}

@media only screen and (max-width: 768px) {
  .aside {
    flex-direction: row;
    justify-content: center;
    width: 100%;
  }
}
