.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 120px;
  margin-bottom: 20px;
  background-color: #cdb4db;
  z-index: 2;
}
.headerContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 80%;
  max-width: 1160px;
  height: 100%;
  margin: 0 auto;
}
.title {
  font-size: 35px;
}
.avatar {
  height: 40%;
  border-radius: 70px;
  border: 1px solid #a2d2ff;
  filter: drop-shadow(0px 0px 6px #f28482);
}
.completedText {
  position: absolute;
  content: "";
  width: 100%;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  padding-right: 10px;
  font-size: 10px;
  text-align: end;
  opacity: 0.3;
}
@media only screen and (max-width: 768px) {
  .header {
    height: 60px;
  }
  .title {
    font-size: 20px;
  }
}
