.currencies {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  height: 117px;
  width: 100%;
  padding: 10px;
  border-radius: 6px;
  background-color: #ffafcc;
}
.currency {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}
.flag {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
}
@media only screen and (max-width: 768px) {
  .currencies {
    width: 49%;
    min-height: 93px;
    height: 100%;
  }
}
