.listContent {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
  z-index: 2;
}
.listText {
  width: 90%;
  margin: 0 auto;
  text-align: left;
  font-size: 12px;
  font-weight: 600;
  height: 85px;
  overflow-y: scroll;
  word-break: break-word;
  line-height: 1.3;
}

.listText::-webkit-scrollbar {
  width: 2px;
  background-color: #f5cac3;
}
.listText::-webkit-scrollbar-thumb {
  background-color: #f28482;
  border-radius: 12px;
}
.listText::-webkit-scrollbar-track {
  background-color: #cdb4db;
}

.date {
  display: block;
  width: 100%;
  border-radius: 6px 6px 0 0;
  font-size: 10px;
  padding: 2px 5px;
  background-color: #cdb4db;
}
.buttons {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
  width: 100%;
  padding: 0px 5px 5px 0px;
  column-gap: 1px;

  position: relative;
  z-index: 2;
}
.listActive {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  width: 49%;
  height: 140px;
  border-radius: 6px;
  row-gap: 6px;
  position: relative;
  overflow: hidden;
  border: 1px solid #cdb4db;
}

.listActive:before {
  content: " ";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0.1;
  background-image: url("../../Images/cat.jpg");
  background-repeat: no-repeat;
  background-position: 25% 40%;
  background-size: cover;
}

.list {
  // opacity: 1;
  background-color: #6a6262;
}
.list .listText::-webkit-scrollbar-track {
  background-color: #6a6262;
}
.list .listText::-webkit-scrollbar-thumb {
  background-color: #4b4a4a;
}
.done,
.del {
  width: 20%;
  height: 20px;
  border-radius: 6px;
  background-color: #a2d2ff;
  transition: all 0.1s ease-in-out;
}
.done:active,
.del:active {
  background-color: #f6bd60;
}
.del {
  background-color: #fb8888;
}
@media (hover: hover) {
  .done:hover {
    background-color: #bde0fe;
  }
  .del:hover {
    background-color: #ffc8c8;
  }
}
