.form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  width: 100%;
  margin-bottom: 10px;
}
.input {
  width: 100%;
  height: 100px;
  margin-bottom: 10px;
  padding: 5px;
  border: 1px solid #84a59d;
  border-radius: 6px;
  resize: none;
  transition: all 0.2s ease-in-out;
  line-height: 1.3;
}
.input:focus {
  background-color: #f7ede2;
}
.input:focus {
  outline: none;
  border-color: #84a59d !important;
}
.input::placeholder {
  opacity: 0.5;
}
.input::-webkit-scrollbar {
  width: 2px;
  background-color: white;
}
.input::-webkit-scrollbar-thumb {
  background-color: #f5cac3;
  border-radius: 12px;
}
.input::-webkit-scrollbar-track {
  background-color: #f7ede2;
  padding-right: 2px;
}
