.uiButton {
  display: block;
  width: 100%;
  height: 50px;
  border-radius: 25px;
  background-color: #a2d2ff;
  transition: all 0.1s ease-in-out;
}
.uiButton:active {
  background-color: #bde0fe;
}
.uiButton:focus {
  border-color: #84a59d !important;
}
.uiButton:disabled {
  background-color: grey;
  opacity: 0.8;
}
@media (hover: hover) {
  .uiButton:hover {
    background-color: #bde0fe;
  }
}
