.section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: left;
  width: 80%;
  min-height: 250px;
  margin: 0 auto;
  padding: 10px;
  background-color: #f7ede2;
  border-radius: 6px;
}

@media only screen and (max-width: 768px) {
  .section {
    width: 100%;
  }
}
